<script>
  import moment from "moment";
  import appStore from "./../../store";
  import Modal from "./../modal";

  let hash = "";
  let open =false;

  appStore.getBlocks();

  async function onSelected(params) {
    hash = params;

    open =true;
    appStore.getByHash(hash)
    document.getElementById("id01").style.display = "block";


  }
  const dateFormat = time => moment(new Date(time), "YYYYMMDD").fromNow();
</script>

<style>
  .paginate{
    margin-top: .5rem;
    display: flex;
    justify-content: center;

  }
</style>

{#if $appStore.status == 'loading'}
  <div>Loading data, please wait...</div>
{:else if $appStore.status == 'done'}
  <div>
    <table class="w3-table-all w3-hoverable w3-card-4 ">
      <tr>
        <th>Height</th>
        <th>Hash</th>
        <th>Mined</th>
      </tr>
      {#each $appStore.pagination as { hash, time, height }}
        <tr>
          <td on:click={() => onSelected(hash)}>
            <a href="javascript:void 0;">{height}</a>
          </td>
          <td on:click={() => onSelected(hash)}>
            <a href="javascript:void 0;">{hash}</a>
          </td>
          <td>{dateFormat(time)}</td>
        </tr>
      {/each}

    </table>
    {#if $appStore.blocks.length > $appStore.pagination.length}
      <div  class="paginate">
      <button on:click={() => appStore.loadMore(15)}> Load More</button>

      </div>
    {/if}

  </div>
{:else}
  <div>Error occured!!</div>
{/if}


 <Modal {hash}  bind:open={open}/>


